import axios from 'axios'

//Backend Toggle

var SERVER = "http://localhost:5000"
//const SERVER = "http://myfirstapp-env.eba-rygdbhqj.us-east-1.elasticbeanstalk.com"

class NCService {

    //Search

    searchGame(id) {
        this.setUpAuthentication();
        return axios.get(this.getDomain() + `/search/${id}`)
    }

    //Two Player

    getDomain() {
        return SERVER
    }

    getTPGame(id) {
        this.setUpAuthentication()
        return axios.get(this.getDomain() + `/tpgames/${id}`)
    }

    createTPGame() {
        this.setUpAuthentication()
        return axios.get(this.getDomain() + `/tpgames/new`)
    }

    doTPMove(id, move) {
        this.setUpAuthentication()
        return axios.post(this.getDomain() + `/tpgames/${id}/move`, move)
    }

    //Computer games

    // createCPGame() {
    //     this.setUpAuthentication()
    //     return axios.get(this.getDomain() + `/cpgames/new`)
    // }

    // createCPGameX() {
    //     this.setUpAuthentication()
    //     return axios.get(this.getDomain() + `/cpgames/new/x`)
    // }


    // getCPGame(id) {
    //     this.setUpAuthentication()
    //     return axios.get(this.getDomain() + `/cpgames/${id}`)
    // }

    // doCPMove(id, move, difficulty) {
    //     this.setUpAuthentication()
    //     return axios.post(this.getDomain() + `/cpgames/${id}/move/${difficulty}`, move)
    // }


    //Authentication

    setUpAxiosInterceptors(token) {

        axios.interceptors.request.use(
            (config) => {
                config.headers.authorization = token
                return config
            }
        )
    }

    createBasicAuthToken(username, password) {
        return 'Basic ' + window.btoa(username + ":" + password)
    }

    setUpAuthentication() {
        
        //For now hard code log in details
        sessionStorage.setItem("authenticatedUser", "sam.weller")
        this.setUpAxiosInterceptors(this.createBasicAuthToken("sam.weller", "sweller"))
    }

}

export default new NCService()
