import Game from "./Game.js"
import MoveRequest from "./MoveRequest.js"
import {AIeval} from './AI.js'

var IDmap = new Map()

export function bestMove(game, depth) {

    //I.e. -1 = perfect move
    if (depth === -1) {
        depth = 1000000
    }

    let moveEvals = new Map()
    let legalMoves = game.legalMoves()
    let i = 0
    for (i = 0; i < legalMoves.length; i++) {
        moveEvals.set(legalMoves[i], 
            AIeval(game, 
                new MoveRequest(legalMoves[i].x, legalMoves[i].y, game.turn ? 1 : 2),
                depth-1))
    }

    //Depending on whos turn it is pick the min or max from this list
    //NB We may want to list all possible moves of the same value
    //and then randomly pick from them to keep it interesting
    
    let moveRandomiser = []

    //First try and get eval equal to your number
    for (let pos of moveEvals.keys()) {
        let value = moveEvals.get(pos)
        let evaluation = game.turn ? 1 : 2
        if (value === evaluation) {
            moveRandomiser.push(pos)
        }
    }
    if (moveRandomiser.length > 0) {
        return moveRandomiser[Math.floor(Math.random()*moveRandomiser.length)]
    }

    
    moveRandomiser = []

    //Then look for draws
    for (let pos of moveEvals.keys()) {
        if (moveEvals.get(pos) === -1) {
            moveRandomiser.push(pos)
        }
    }
    if (moveRandomiser.length > 0) {
        return moveRandomiser[Math.floor(Math.random()*moveRandomiser.length)]
    }

    moveRandomiser = []

    //Then look for unknown (only possible when there's a depth issue)
    for (let pos of moveEvals.keys()) {
        if (moveEvals.get(pos) === 0) {
            moveRandomiser.push(pos)
        }
    }
    if (moveRandomiser.length > 0) {
        return moveRandomiser[Math.floor(Math.random()*moveRandomiser.length)]
    }

    moveRandomiser = []

    //If nothing else then just return a losing move
    for (let pos of moveEvals.keys()) {
        moveRandomiser.push(pos)
    }
    if (moveRandomiser.length > 0) {
        return moveRandomiser[Math.floor(Math.random()*moveRandomiser.length)]
    }

    return null

}

export function getArray(game) {
    return [game.board.board[0],
    game.board.board[1],
    game.board.board[2],
    game.board.board[3],
    game.board.board[4],
    game.board.board[5],
    game.board.board[6],
    game.board.board[7],
    game.board.board[8],
    game.turn
]
}

export function getStr(game) {
    return game.board.board[0].toString() + game.board.board[1].toString() + game.board.board[2].toString() + game.board.board[3].toString() + game.board.board[4].toString() + game.board.board[5].toString() + game.board.board[6].toString() + game.board.board[7].toString() + game.board.board[8].toString() + game.turn.toString()
}

export function AIevalFast(game, move, depth) {

    let newGame = new Game()
    newGame.setAs(game)

    if (depth > 0) {
        newGame.moveByRequest(move)
        let strKey = getStr(newGame)
        if (IDmap.has(strKey)) {
            return IDmap.get(strKey)
        }
        if (newGame.winner !== 0) {
            return newGame.winner
        } else {
            let moveEvals = new Map()
            let legalMoves = newGame.legalMoves()
            
            for (let i = 0; i < legalMoves.length; i++) {
                moveEvals.set(legalMoves[i], 
                    AIeval(newGame, 
                        new MoveRequest(legalMoves[i].x, legalMoves[i].y, move.player === 1 ? 2 : 1),
                        depth-1))
            }

            //First try and get eval equal to your number
            for (let pos of moveEvals.keys()) {
                let value = moveEvals.get(pos)
                let evaluation = newGame.turn ? 1 : 2
                if (value === evaluation) {
                    return moveEvals.get(pos)
                }
            }

            //Then look for draws
            for (let pos of moveEvals.keys()) {
                if (moveEvals.get(pos) === -1) {
                    return -1
                }
            }

            //Then look for unknown (only possible when there's a depth issue)
            for (let pos of moveEvals.keys()) {
                if (moveEvals.get(pos) === 0) {
                    return 0
                }
            }

            //If nothing else then just return a losing move
            for (let pos of moveEvals.keys()) {
                return moveEvals.get(pos)
            }
        }
    } else {
        return 0
    }

    return 0

}

export function AIevalbar(game) {

    let moveEvals = new Map()
    let legalMoves = game.legalMoves()
    if (legalMoves === null) {
        return game.winner
    }
    let i = 0
    for (i = 0; i < legalMoves.length; i++) {
        moveEvals.set(legalMoves[i], 
            AIeval(game, 
                new MoveRequest(legalMoves[i].x, legalMoves[i].y, game.turn ? 1 : 2),
                10000))
    }

    //First try and get eval equal to your number
    for (let pos of moveEvals.keys()) {
        let value = moveEvals.get(pos)
        let evaluation = game.turn ? 1 : 2
        if (value === evaluation) {
            return value
        }
    }

    //Then look for draws
    for (let pos of moveEvals.keys()) {
        if (moveEvals.get(pos) === -1) {
            return -1
        }
    }

    //Then look for unknown (only possible when there's a depth issue)
    for (let pos of moveEvals.keys()) {
        if (moveEvals.get(pos) === 0) {
            return 0
        }
    }

    //If nothing else then just return a losing move
    return game.turn ? 2 : 1

}

export function getComplex(i) {
    let a = (i-(i % 9))/9
    let b = (i-9*a) % 3
    let c = ((i-9*a) - b) / 3
}

export function run(i) {
    //This function runs the workload in chunks so the user can see a progress bar
    //It takes 3.5s to run on my machine.  Dividing into 27 chunks for ~0.1s progress bar which should be satisfactory even for mobile devices
    let a = (i-(i % 9))/9
    let b = (i-9*a) % 3
    let c = ((i-9*a) - b) / 3

    for (let d = 2; d >= 0; d--) {
        for (let e = 2; e >= 0; e--) {
            for (let f = 2; f >= 0; f--) {
                for (let g = 2; g >= 0; g--) {
                    for (let h = 2; h >= 0; h--) {
                        for (let i = 2; i >= 0; i--) {
                            let game = new Game()
                            game.board.board = [a,b,c,d,e,f,g,h,i]
                            let strKey = a.toString() + b.toString() + c.toString() + d.toString() + e.toString() + f.toString() + g.toString() + h.toString() + i.toString() + "false"
                            game.turn = false
                            if (!IDmap.has(strKey)) {
                                IDmap.set(strKey, AIevalbar(game))
                            }
                            game.turn = true
                            strKey = a.toString() + b.toString() + c.toString() + d.toString() + e.toString() + f.toString() + g.toString() + h.toString() + i.toString() + "true"
                            if (!IDmap.has(strKey)) {
                                IDmap.set(strKey, AIevalbar(game))
                            }
                        }
                    }
                }
            }
        }
    }

    return IDmap
}